* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}

.header {
  text-align: center;
  background-color: #5355e4;
  min-height: 20%;
  min-width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  position: sticky;
  top: 0;
  margin-bottom: 0;
  margin: 0;
  padding: 0;
  z-index: 1;
  border-bottom: #000000 solid 2px;
}
/* 
.header #my-links{
  display: none;
} */

/* .App-link {
  color: #61dafb;
} */
.footer {
  background-color: #000000;
  min-height: 5%;
  min-width: 100%;
  display: flex;
  flex-direction: column;
  /* align-items: center;
  justify-content: center; */
  font-size: calc(10px + 2vmin);
  color: white;
  /* position: sticky; */
  bottom: 0;
  margin: 0;
  position: relative;
  z-index: 1;

}

.footer-text {
  text-align: left;
}

.footer-icon {
    align-content: right;
    text-align: right;
    height: 45px;
    width: 45px;
    right: 20px;
    
}


.header-buttons {
  margin: 5px;
  background-color: #209bd7;
  border: 2px solid rgb(0, 0, 0);
  margin-top: 16px;
  margin-bottom: 16px;
  border-radius: 25px;
  text-align: center !important;

  font-size: 25px;
  width: 11%;
  min-width: 11%;
  min-height: 70px;
  text-align: center;
  transition: .2s ease;
  display: inline-flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.header-buttons:hover{
  scale: 95%;
  
  cursor: pointer;
}

.static-background {
  position:fixed;
  top: 0;
  left:0;
  height: 100%;
  width: 100%;
  object-fit: cover;
  z-index: -2;
}

.mobile-only{
  display: none;
  justify-content: center;
  align-items: center;
  align-self: center;
  margin: 0 auto;
}

.page-background {
  background-color: #209ad7f1;
  min-height: 100vh;
  background-size: cover;
  
  margin: 0;
  padding: 0;
  margin-left: 5%;
  margin-right: 5%;
  margin-top: 0%;
  position: relative;
  z-index: 0;
  border-left: #0000003d groove 15px;
  border-right: #0000003d ridge 15px;
}


.content-container {
  margin-left: 1%;
  margin-right: 1%;
  padding-top: 2%;
  
  text-align: center;
  align-content: center;
  display: flex;
  position: relative;
  float: none;
  flex-wrap: wrap;
  justify-content: space-between;
  
}

.container-row {
  display:table;
        clear:both;
        width:100%;
        content:"";
}


.container-col-left{
  width: 40%;
float:left;
}

.container-col-right{
  width: 60%;
  float:left;

}

.component-container{
  margin: 2%;
  padding: 2%;
  background-color: #ffffff;
  width: 95%;
  height: min-content;
  position: relative;
  overflow: hidden;
  text-align: center;
  align-items: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  overflow: hidden;
  border-radius: 20px;
  border-color: #00000069;
  border-width: 4%;
  border-style:outset;
  transition: ease .1s;
  color: white;
}

.component-container.alt{
  
  background-color:#00000000;
  border-width:0px;
  padding-top:60px;
  padding-bottom:60px;
  padding-left:60px;
  overflow:visible;
  justify-content:center;


}


@media screen and (max-width: 1000px) {
  .container-col-left {
    width: 100% !important;
  }
  .container-col-right {
    width: 100% !important;
  }
  .component-container {
    width: 100%; /* Stack containers on smaller screens */
    margin: 2% 0; /* Adjust margin for stacked layout */
  }
  .page-background{
    background-color: #00000000 !important;
    border-left: 0px;
    border-right: 0px;
  }
  .header-buttons{
    width: 80%;
    margin: 2% 0;
    
  }
  .header{
    position: static;
  }
  .header-buttons-cont {
    
    flex-direction: column;
  }
  .mobile-only{
    display: block;
  }

}
.bio-icon{
  width: 60%;
  height: auto;
  position: relative;
  border-radius: 20px;
  z-index: 4;
}

.icon-splash{
  width: 75%;
  height: auto;
  position:absolute;
  border-radius: 20px;
  z-index: 3;
  left: 0;
  /* top: 0; */
}

.player-name{
  font-size:90px;
  text-transform: uppercase;

}

.video-container {
  
  position: relative;
  width: 100%;
  max-width: 1280px;
  padding-top: 56.25%;
  overflow: hidden;
  border-radius: 20px;
}

.video-element {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: transform .5s ease-in-out;
  object-fit: cover;
  transform: translateX(100%); /* Start offscreen to the right */

}

.video-element.active {
  transform: translateX(0); /* Slide into view */
}

.video-element.exit {

  transform: translateX(-100%); /* Slide out to the left */
}

.weapon-container{
  background-color:#ffffff21 !important;
  display: inline-flex;
  flex-direction: row;
  
  margin: 2px;
  margin-left: 10px;
  margin-right: 10px;
  border-color: #000000a2;
  border-radius: 8px;
  border-width: 0px;
  border-style: solid;
  padding: 2px;
}

